import './App.css';
import Sidebar from './Sidebar';
import uuid from 'react-uuid';
import Main from './Components/Main';
import React from 'react'
import data from './JSON/data.json';

class App extends React.Component {

  constructor(props) {
    super(props);
    this.obj = require("./JSON/data.json");
    this.state = {
      notes: JSON.parse(JSON.stringify(data)),
      activeNoteId: "0"
    };
  }



  exportData = () => {
    const jsonString = `data:text/json;chatset=utf-8,${encodeURIComponent(
      JSON.stringify(this.state.notes)
    )}`;
    const link = document.createElement("a");
    link.href = jsonString;
    link.download = "data.json";

    link.click();
  }

  componentDidUpdate() {
  }
  

  onAddNote = (specificId) => {
    const randomUUID = uuid();
    const newNote = {
      id: specificId || randomUUID,
      title: "ðŸ“„Nouvelle page",
      contente: "SALUT",
      body: [
        {
            id: "0",
            content: '/help pour dÃ©couvrir',
            style: {
              "textDecoration":"none",
              "fontWeight": "normal"
            }
        }
    ]
    };
    if (this.state.notes.length === 0) {
      newNote.title = "ðŸ˜ï¸ Bienvenue !";
      newNote.id = "0";
      this.setState({
        notes: [newNote],
        activeNoteId: newNote.id
      });
    } else {
      this.setState((prevState) => ({
        notes: [newNote, ...prevState.notes],
        activeNoteId: newNote.id
      }));
    }
    return newNote;
  };

  setActiveNote = (noteId) => {
    this.setState({ activeNoteId: noteId });
  };

  getActiveNote = () => {
    let activeNote = this.state.notes.find((note) => note.id === this.state.activeNoteId);
    if (!activeNote) {
      activeNote = this.onAddNote();
    }
    return activeNote;
  };

  render() {
    return (
      <div className="App">
        <Sidebar exportation={this.exportData} notes={this.state.notes} onAddNote={this.onAddNote} activeNoteId={this.state.activeNoteId} setActiveNote={this.setActiveNote} />
        <Main activeNote={this.getActiveNote()} setActiveNote={this.setActiveNote}/>
      </div>
    );
  }
  
}

export default App;
