import React, { useRef, useEffect } from 'react';
import '../Styles/Bloc.css';
import TextareaAutosize from 'react-textarea-autosize';

function Bloc({ change, val, keyed, id, autof, delet, styles }) {

  const handleKeyPress = (e) => {

    console.log("clicked");
    if (e.nativeEvent.keyCode === 13) {
      if (!e.nativeEvent.shiftKey) {
        if (val === '/page') {
          e.preventDefault();
        } else {
          keyed(id);
          e.preventDefault();
        }
      }
    } else if (e.nativeEvent.keyCode === 8) {
      if (val === '' && id !== 0) {
        delet(id);
      }
    }
  };

  if (autof) {
    return (
      <div>
        <TextareaAutosize
          onKeyDownCapture={(ev) => {
            handleKeyPress(ev);
          }}
          id="bloc"
          style={styles}
          value={val}
          name="active"
          onChange={change}
          spellCheck={false}
        />
      </div>
    );
  } else {
    return (
      <div>
        <TextareaAutosize
          onKeyDownCapture={(ev) => {
            handleKeyPress(ev);
          }}
          id="bloc"
          style={styles}
          value={val}
          onChange={change}
          spellCheck={false}
        />
      </div>
    );
  }
}

export default Bloc;